html, body{
  margin: 0px;
  padding: 0px;
  font-family: "Ubuntu", sans-serif;
}

/* chatbot container */
.chatbot {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: minmax(220px,2fr) 3fr;
  grid-template-rows: 60px auto;
  grid-template-areas:
  "channelsheader chatsheader"
  "channels chats";
}

.channel-header-container {
  grid-area: "channelsheader";
  background-color: steelblue;
  color: white;
  border-right: 1px solid lightgrey;
  border-bottom: 1px solid lightgrey;
}

.chat-header-container {
  grid-area: "chatsheader";
  background-color: steelblue;
  border-bottom: 1px solid lightgrey;
}

.channel-container {
  grid-area: "channels";
  overflow-y: auto;
  border-right: 1px solid lightgrey;
}

.chat-container {
  grid-area: "chats";
  background-color: white;
  overflow-y: auto;
}


/* chat header */
.chat-header {
  height: 100%;
  display: grid;
  grid-template-columns: auto max-content max-content;
  grid-template-areas:
  "title button button";
  align-items: center;
}

.chat-header-title {
  grid-area: "title";
  margin: 0px 10px 0px 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.chat-header-button-container {
  grid-area: "button";
  margin: 0px 5px 0px 0px;
}

.chat-header-button {
  all: inherit;     /* remove all button styles */
  cursor: pointer;
  font-size: 13px;
  padding: 15px 15px 15px 15px;
  color: white;
  background-color: steelblue;
}

.chat-header-button:hover {
  color: black;
}

.channel-header {
  margin: 20px 0px 0px 20px;
  cursor: default;
}

/* channel list */
.channel-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

/* channel list button */
.channel-list-button {
  all: inherit;     /* remove all button styles */
  width: 100%;
  cursor: pointer;
  outline: none;
}

.channel-list-button:hover {
  background-color: lightgreen;
}

.channel-list-button.active {
  background-color: #F4F4F4;
}


/* channel list item */
.channel-item-container {
  display: grid;
  grid-template-columns: 15px 70px auto auto;
  grid-template-rows: 35px 35px;
  grid-template-areas:
  "status image headline time"
  "status image subline  unread";
  border-bottom: 1px dotted lightgrey;
}

.channel-item-status.active {
  grid-area: status;
  background-color: lightgreen;
}

.channel-item-image {
  grid-area: image;
  border-radius: 50%;
  margin: 10px 10px 10px 10px;
  background-color: #fb4f57;
  border: 1px solid black;
}

.channel-item-headline {
  grid-area: headline;
  margin: 0px 5px 0px 5px;
  align-self: end;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.channel-item-time {
  grid-area: time;
  justify-self: end;
  margin: 5px 5px 5px 5px;
  font-size: 11px;
  color: grey;
}

.channel-item-subline {
  grid-area: subline;
  margin: 3px 20px 0px 5px;
  font-size: 13px;
  color: grey;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.channel-item-unread {
  grid-area: unread;
  align-self: start;
  justify-self: end;
  margin: 0px 10px 0px 0px;
  padding: 3px 3px 3px 3px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  font-size: 13px;
  color: white;
  background-color: lightgreen;
}
