
/* chat container */
.chat {
  height: 100%;
  display: grid;
  grid-template-rows: 1fr 50px;
  grid-template-areas:
  "chatdialog"
  "footer";
}

.chat-dialog {
  grid-area: "chatdialog";
  overflow-y: auto;
}

.chat-footer {
  grid-area: "footer";
  background-color: white;
  border-top: 1px solid lightgrey;
}


/* chat dialog list */
.chat-dialog-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}


/* chat dialog list container */
.chat-container-left {
  display: grid;
  grid-template-columns: minmax(auto, 460px) minmax(20px, auto);
  grid-template-areas:
  "item gap";
  justify-items: start;
}

.chat-container-right {
  display: grid;
  grid-template-columns: minmax(20px, auto) minmax(auto, 460px);
  grid-template-areas:
  "gap item";
  justify-items: end;
}

/* '>' = selector for next child element */
.chat-container-left > div {
  background-color: #fb4f57;
}
.chat-container-right div {
  background-color: lightgrey;
  color: black;
}


/* chat dialog list container items */
/* type: text */
.chat-item {
  grid-area: item;
  display: grid;
  grid-template-rows: auto;
  grid-template-areas:
  "text"
  "time";
  margin: 5px 5px 5px 5px;
}

.chat-item-text {
  grid-area: text;
  margin: 10px 10px 0px 10px;
  font-size: 13px;
}

.chat-item-time {
  grid-area: time;
  justify-self: end;
  margin: 3px 10px 5px 0px;
  font-size: 10px;
  color: white;
}

.speech-bubble {
  -webkit-border-radius: 4px;
  border-radius: 4px;
  line-height: 1.3;
  border: 1px solid black;
}

/* type: image */
.chat-image {
  grid-area: item;
  margin: 5px 5px 5px 5px;
  max-width: 100%;
  height: auto;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  position: relative;
}

/*  type: selection */
.chat-selection {
  grid-area: item;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: auto 30px;
  grid-template-areas:
  "text edit"
  "options options"
  ". time";
  margin: 5px 5px 5px 5px;
  border: 1px solid black;
}

.chat-selection-edit {
  all: inherit;     /* remove all button styles */
  grid-area: edit;
  height: 15px;
  margin: 5px 10px 3px 0px;
  cursor: pointer;
  border: none;
  font-size: 10px;
  color: white;
}





.chat-selection-list {
  grid-area: options;
  margin: 5px 15px 5px 20px;
  padding: 0;
  list-style-type: none;
}

.hide {
  display: none;
}


.chat-selection-button {
  all: inherit;     /* remove all button styles */
  width: 100%;
  cursor: pointer;
  outline: none;
  margin: 2px 2px 5px 2px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  font-size: 13px;
  border: 1px solid lightgrey;
  color: black;
  background-color: white;
}

.chat-selection-button.selected {
  pointer-events: none;
  border: 1px solid black;
  background-color: lightgrey;
}

.chat-selection-button:hover {
  border: 1px solid black;
  background-color: lightgrey;
}

.chat-selection-button-text {
  margin: 5px 5px 5px 5px;
}
